import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';

import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import { useStyles } from '../styles/Styles';

function DreamMap(props) {
    const { lang, themeMode, history } = props;
    const classes = useStyles();
    const muiTheme = createMuiTheme(themeMode);
    const [cellHeight, setcellHeight] = React.useState(25);
    const [cellWidth, setcellWidth] = React.useState(25);

    console.log(cellWidth, cellHeight);

    const sizeUp = () => {
        setcellHeight(cellHeight + 5);
        setcellWidth(cellWidth + 5);
    };

    const sizeDown = () => {
        setcellHeight(cellHeight - 5);
        setcellWidth(cellWidth - 5);
    };

    const createTable = () => {
        let table = []

        for (let i = 0; i < 20; i++) {
            let children = [];
            for (let j = 0; j < 20; j++) {
                children.push(
                    <td key={'cell' + i + j}
                        style={{
                            height: cellHeight + 'px',
                            minWidth: cellWidth + 'px',
                            maxWidth: cellWidth + 'px',
                            textAlign: 'center',
                            border: '1px black solid',
                            transition: "all 0.3s linear",
                        }}
                        onClick={() => { alert(i + ' ' + j) }}
                    />
                )
            }
            table.push(
                <tr key={'row' + i} >
                    {children}
                </tr>
            )
        }
        return table;
    }

    return (
        <MuiThemeProvider theme={muiTheme}>
            <CssBaseline />
            <div className={classes.root}>
                <Grid className={classes.mainGridContainer}
                    container
                    direction="column"
                    justify="center"
                    alignItems="stretch" >
                    <Grid item xs={11} zeroMinWidth className={classes.mainGridDreamsBodyItem}>
                        <Container
                            style={{
                                paddingTop: '16px',
                                height: '90%',
                                width: '100%',
                                position: 'absolute',
                            }}
                        >
                            <div
                                style={{
                                    position: 'relative',
                                    height: '100%',
                                    width: '100%',
                                    overflow: 'auto',
                                }}
                            >
                                <table
                                    style={{
                                        margin: '0 auto',
                                        backgroundColor: 'green',
                                    }}
                                >
                                    <tbody>
                                        {createTable()}
                                    </tbody>
                                </table>
                            </div>
                            <div
                                style={{
                                    position: 'absolute',
                                    bottom: 0,
                                    left: '50%',
                                    transform: 'translateX(-50%)',
                                }}
                            >
                                <Fab
                                    size="small"
                                    color="primary"
                                    aria-label="add"
                                    onClick={() => { sizeUp() }}
                                >
                                    <AddIcon />
                                </Fab>
                                <Fab
                                    size="small"
                                    color="primary"
                                    aria-label="add"
                                    onClick={() => { sizeDown() }}
                                >
                                    <RemoveIcon />
                                </Fab>
                            </div>
                        </Container>
                    </Grid>
                    <Grid item xs={1} zeroMinWidth className={classes.mainGridBodyItem}>
                        <Grid
                            container
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                            spacing={1}
                        >
                            <Grid item xs={6} align="center">
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    className={classes.actionButton}
                                    onClick={() => { history.push("/luciddreams") }}
                                >
                                    {lang.currLang.buttons.close}
                                </Button>
                            </Grid>
                            <Grid item xs={6} />
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </MuiThemeProvider >
    )
}

DreamMap.propTypes = {
    themeMode: PropTypes.object.isRequired,
    lang: PropTypes.object.isRequired,
};

const mapStateToProps = store => {
    return {
        themeMode: store.themeMode,
        lang: store.lang,
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DreamMap);